import { useState } from 'react';
import { UserOutlined, DownOutlined } from '@ant-design/icons';
import { Button, Dropdown, Space } from 'antd';
import Search from '../content/Search';
import Road from '../content/Road';

function HeaderMenu({ onHandleModal, openLayers, toggleGamburger }) {
  const tools = [
    {
      label: (
        <div onClick={() => onHandleModal(<Search />)}> </div>
      ),
      key: '1',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: ' / ',
      key: '2',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: ' ',
      key: '3',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: ' ',
      key: '4',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: '',
      key: '5',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: '',
      key: '6',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
    {
      label: <div onClick={() => onHandleModal(<Road />)}></div>,
      key: '7',
      icon: <UserOutlined />,
      onClick: toggleGamburger,
    },
  ];

  const regions = [
    {
      label: '. ',
      key: '1',
      children: [
        {
          label: ' ',
          key: '11',
          children: [
            {
              label: '',
              key: '111',
            },
            {
              label: '',
              key: '112',
            },
            {
              label: '',
              key: '113',
            },
          ],
        },
        {
          label: ' ',
          key: '12',
          children: [
            {
              label: '',
              key: '121',
            },
            {
              label: '',
              key: '122',
            },
            {
              label: '',
              key: '123',
            },
          ],
        },
        {
          label: ' ',
          key: '13',
          children: [
            {
              label: '',
              key: '131',
            },
            {
              label: '',
              key: '132',
            },
            {
              label: '',
              key: '133',
            },
          ],
        },
        {
          label: ' ',
          key: '14',
          children: [
            {
              label: '',
              key: '141',
            },
            {
              label: '',
              key: '142',
            },
            {
              label: '',
              key: '143',
            },
          ],
        },
      ],
    },
  ];

  return (
    <div className="headerMenu">
      <Button
        type="text"
        onClick={openLayers}
      >
        
      </Button>
      <Dropdown menu={{ items: tools }}>
        <Button type="text">
          <Space>
            
            <DownOutlined />
          </Space>
        </Button>
      </Dropdown>
      <Button
        type="text"
        onClick={() => onHandleModal(<Search />)}
      >
        
      </Button>
      <Dropdown menu={{ items: regions }}>
        <Button type="text">
          <Space>
            
            <DownOutlined />
          </Space>
        </Button>
      </Dropdown>
    </div>
  );
}

export default HeaderMenu;
